import { LocalMall } from '@mui/icons-material';
import React, { useState } from 'react';
import Address from './Address';
import datas from './Assets/Data'
import {Container,ClothsContain,Wrapper,ClothsWrap,ClothsImage,
        OfferSection,H4,PriceSection,ButtonSection,Button}from './ClothElement';

const Cloths = () => {
  return (
    <Container>
      <Address/>
        <ClothsContain>
          <Wrapper>
            {
              datas && datas.map((data)=>{
                return(
                  <ClothsWrap key={data.index}>
                    <ClothsImage src={data.img} alt="image"></ClothsImage>
                    <h4>{data.name}</h4>
                    <PriceSection>
                      <OfferSection>
                        <h4>&#8377; {data.price[0]}.00</h4>
                        <H4>&#8377; {data.price[1]}.00</H4>
                      </OfferSection>
                      <ButtonSection>
                        <Button><LocalMall/>CART</Button>
                      </ButtonSection>
                    </PriceSection>
                  </ClothsWrap>
                )
              })
            }
          </Wrapper>
      </ClothsContain>

    </Container>
  )
}
export default Cloths;