import React, { useState } from 'react';
import { Container,Contain,P,Icon,AnsWrap } from './FaqElement';

const FaqDetails = ({question,answer}) => {
    const [show, setShow] = useState(false);
  return (
    <Container>
        <Contain>
        <P onClick={() => setShow(!show)}>{show?<Icon>-</Icon>:<Icon>+</Icon>}</P>
        <h3>{question}</h3>
        </Contain>
        <AnsWrap>
        { show && <p>{answer}</p>}
        </AnsWrap>
        
    </Container>
  )
}

export default FaqDetails