import { LocalMall } from '@mui/icons-material';
import React from 'react';
import datas from './Data/Data'
import {Container,AddressContain,Image,IconSection,Icon,WebContain,ContactSection,ProductContain,
    BannerImage,ProductItem,Wrapper,ProductWrap,ProductImage,H5,
    OfferSection,H4,PriceSection,Button,LogoSection}from './ProductElement';

const Product = () => {
  return (
    <Container>
              <ProductContain>
        <BannerImage src={require("./Data/images/Makeup.jpg")}></BannerImage>
        <LogoSection>
          <Image src={require("./Data/images/Makeup.jpg")}></Image>
          <div>
            <h3>Bakery Shop</h3>
            <p>More Info</p>
          </div>

        </LogoSection>
        <ProductItem>
          <Wrapper>
            {
              datas && datas.map((data,id)=>{
                return(
                  <ProductWrap key={data.id}>
                    <ProductImage src={data.img} alt="image"></ProductImage>
                    <h3>{data.name}</h3>
                    <H5>1 Pc(s)</H5>
                    <OfferSection>
                      <H4>&#8377; {data.price[1]}.00</H4>
                      <h4>{data.price[2]}% off</h4>
                    </OfferSection>
                    <PriceSection>
                      <h3>&#8377; {data.price[0]}.00</h3>
                      <Button><LocalMall/>CART</Button>
                    </PriceSection>
                  </ProductWrap>
                  
                )
              })
            }
          </Wrapper>
        </ProductItem>
      </ProductContain>

    </Container>
  )
}

export default Product;