import styled from 'styled-components';

export const Container = styled.div`
 width: 70vw;
 margin: 25px;
 background-color:white;

`;
export const Wrapper = styled.div`
 display: flex;
 flex-wrap: wrap;
 align-items: center;
`;
export const ProductWrap = styled.div`
 box-shadow: 0 4px 8px 0 rgba(0,0,0,0.15);
 padding: 15px;
 width:200px;
 height: 355px;
 align-items: center;
 margin:25px;
 background-color: #ffff;
`;
export const OfferSection = styled.div`
 display: flex;
 margin:-20px 0 20px;
`;
export const ProductImage = styled.img`
 width:200px;
 height:200px;
 cursor: pointer;
 &:hover{
     transform: translateY(2px);
 }
`;
export const H4 = styled.h5`
 text-decoration: line-through;
 margin-left: 10px;
`;
export const PriceSection = styled.div`
 display: flex;
 justify-content:space-between;
 align-items: center;
 margin-top:-20px;
`;
export const Button = styled.button`
 display: flex;
 justify-content: space-between;
 align-items: center;
 width: 90px;
 height:30px;
 border-radius: 100px;
 font-size: 12px;
 border:none;
 border: 1px solid;
 padding: 15px;
 cursor: pointer;
 transition: all 0.5s;
 &:hover{
     background-color: grey;
     color: white;
 }
`;
export const LogoSection = styled.div`
 display: none;
 @media screen and (max-width:700px){
     display:flex;
     justify-content: center;
     align-items: center;
 }
`;
export const ButtonSection = styled.div`
 display: flex;
`;
export const Icons = styled.div`
 border: 1px solid ;
 width: 20px;
 height:20px;
 margin-top: 5px;
 align-items: center;
 display: flex;
 justify-content: center;
 border-radius: 50%;
 cursor: pointer;
`;
export const Input = styled.input`
 width: 30px;
 height:20px;
 align-items: center;
 text-align:center;
 margin:3px 5px 0;
 border-radius:5px;
 cursor: pointer;
 border:1px solid;
 outline: none;
`;