import { Facebook, Instagram, LocalMall, Twitter } from '@mui/icons-material';
import React from 'react';
import datas from './Data/Data'
import {Container,AddressContain,Image,IconSection,Icon,WebContain,ContactSection,ProductContain,
  BannerImage,ProductItem,Wrapper,ProductWrap,ProductImage,H5,
  OfferSection,H4,PriceSection,Button,LogoSection}from './MakeupElement';

const Makeup = () => {
  return (
    <Container>
      <AddressContain>
        <Image src={require("../../../assets/images/Makeuplogo.jpg")}></Image>
        <h3>Makeup Shop</h3>
        <p>The makeup shop is the best shop around the city.</p>
        <IconSection>
          <Icon>
            <Facebook/>
          </Icon>
          <Icon>
            <Instagram/>
          </Icon>
          <Icon>
            <Twitter/>
          </Icon>
        </IconSection>
        <hr></hr>
        <ContactSection>
        <h4>Address</h4>
        <p>krmg</p>
        <h4>Phone</h4>
        <p>+91 98765 43210</p>
        <h4>Website</h4>
        <WebContain>
          <p>https//makeupshop./</p>
          <h4>Visit This Site</h4>
        </WebContain>

        </ContactSection>
      </AddressContain>


      <ProductContain>
        <BannerImage src={require("./Data/images/Makeup.jpg")}></BannerImage>
        <LogoSection>
          <Image src={require("../../../assets/images/Makeuplogo.jpg")}></Image>
          <div>
            <h3>Makeup Shop</h3>
            <p>More Info</p>
          </div>

        </LogoSection>
        <ProductItem>
          <Wrapper>
            {
              datas && datas.map((data,id)=>{
                return(
                  <ProductWrap key={data.id}>
                    <ProductImage src={data.img} alt="image"></ProductImage>
                    <h3>{data.name}</h3>
                    <H5>1 Pc(s)</H5>
                    <OfferSection>
                      <H4>&#8377; {data.price[1]}.00</H4>
                      <h4>{data.price[2]}% off</h4>
                    </OfferSection>
                    <PriceSection>
                      <h3>&#8377; {data.price[0]}.00</h3>
                      <Button><LocalMall/>CART</Button>
                    </PriceSection>
                  </ProductWrap>
                  
                )
              })
            }
          </Wrapper>
        </ProductItem>
      </ProductContain>

    </Container>

  )
}

export default Makeup;