import { Facebook, Instagram, LocalMall, Twitter } from '@mui/icons-material';
import React, { useState } from 'react';
import {Container,AddressContain,Image,IconSection,Icon,WebContain,ContactSection,ProductContain,
  BannerImage,ProductItem,Wrapper,ProductWrap,ProductImage,H5,
  OfferSection,H4,PriceSection,Button,LogoSection}from './GroceryElement';
import data from './Data/Data';
import Product from './Product';

const Grocery = () => {
  const [cart, setCart] = useState([]);
  const [ items,setItems ] = useState(data);

  
  const addToCart = index => {
    setItems(state =>
      state.map((item, p) => {
        if (index === p) {
          setCart([
            ...cart,
            { name: item.name, price: item.price[0], quantity: item.quantity }
          ]);
          return { ...item, inCart: true };
        }
        return item;
      })
    );
  };
  const increaseQuantity = {
    Cart: i => {
      setCart(state =>
        state.map((item, o) => {
          if (i === o && item.quantity < 10) {
            return { ...item, quantity: item.quantity + 1 };
          }
          return item;
        })
      );
    },
    Items: i => {
      setItems(state =>
        state.map((item, o) => {
          if (o === i && item.quantity < 10) {
            return { ...item, quantity: item.quantity + 1 };
          }
          return item;
        })
      );
    }
  };

  const decreaseQuantity = {
    inCart: i => {
      setCart(prevCart =>
        prevCart.map((item, o) => {
          if (i === o && item.quantity > 1) {
            return { ...item, quantity: item.quantity - 1 };
          }
          return item;
        })
      );
    },
    inItems: i => {
      setItems(state =>
        state.map((item, o) => {
          if (i === o && item.quantity > 1) {
            return { ...item, quantity: item.quantity - 1 };
          }
          return item;
        })
      );
    }
  };


  return (
    <Container>
      <AddressContain>
        <h3>Grocery Shop</h3>
        <p>The Grocery shop is the best shop around the city.</p>
        <IconSection>
          <Icon>
            <Facebook/>
          </Icon>
          <Icon>
            <Instagram/>
          </Icon>
          <Icon>
            <Twitter/>
          </Icon>
        </IconSection>
        <hr></hr>
        <ContactSection>
        <h4>Address</h4>
        <p>krmg</p>
        <h4>Phone</h4>
        <p>+91 98765 43210</p>
        <h4>Website</h4>
        <WebContain>
          <p>https//Groceryshop./</p>
          <h4>Visit This Site</h4>
        </WebContain>

        </ContactSection>
      </AddressContain>
      <Product 
       items={items}
       increaseCount={increaseQuantity.Items}
       decreaseCount={decreaseQuantity.Items}
       addToCart={addToCart}
      />



    </Container>

  )
}

export default Grocery;