// import { Search } from '@mui/icons-material';
import SearchIcon from '@mui/icons-material/Search';
import React from 'react';
import {Container,Contain,Input,Button} from './SearchElement';

const Search = ({ value,changeInput }) => {
  return (
    <Container>
      <Contain>
        <Input type="text" placeholder='Search here your Products...' value={value} onChange={changeInput}></Input>
        <Button><SearchIcon/>Search</Button>
      </Contain>
    </Container>
  )
}

export default Search;