import styled from 'styled-components';

export const Container = styled.div`
 display: flex;
 width: 100vw;
 background-color:#f5f5Dc;
`;
export const ClothsContain = styled.div`
 width: 70vw;
 background-color:white;
`;
export const Wrapper = styled.div`
 display: flex;
 flex-wrap: wrap;
 align-items: center;
`;
export const ClothsWrap = styled.div`
 box-shadow: 0 4px 8px 0 rgba(0,0,0,0.15);
 padding: 10px;
 width:220px;
 height: 310px;
 align-items: center;
 margin:10px 20px;
 background-color: #ffff;
`;
export const OfferSection = styled.div`
 display: flex;
 margin:-20px 0 20px;
`;
export const ClothsImage = styled.img`
 width:200px;
 height:200px;
 cursor: pointer;
 &:hover{
     transform: translateY(2px);
 }
`;
export const H4 = styled.h5`
 text-decoration: line-through;
 margin-left: 10px;
`;
export const PriceSection = styled.div`
 display: flex;
 justify-content:space-between;
 align-items: center;
`;
export const ButtonSection = styled.div``;
export const Button = styled.button`
 display: flex;
 justify-content: space-between;
 align-items: center;
 width: 90px;
 height:30px;
 border-radius: 100px;
 font-size: 12px;
 border:none;
 border: 1px solid;
 padding: 15px;
 cursor: pointer;
 transition: all 0.5s;
 margin-top:-35px;
 &:hover{
     background-color: grey;
     color: white;
 }
`;
