import React from 'react';
import Search from './Search/index';
// import Filter from './Filter/index';
import Product from './Product/index';
import {Contain,ProductContain} from './HomeElement';

const Home = () => {
  return (
    <>
    <Search/>
    <Contain>
      {/* <FilterContain>
        <Filter/>
      </FilterContain> */}
      <ProductContain>
        <Product/>
      </ProductContain>
    </Contain>
    </>
  )
}

export default Home;