import React from 'react';
import { Link } from 'react-router-dom';
import { Container,Contain,H1,Span,Form,Label,Input,Inputs,Div } from './LoginElement';

const Login = () => {
  return (
    <Container>
      
      
      <Form>
        <Contain>
        <H1>Super<Span>Market</Span></H1>
        <h3>Login to admin</h3>
        </Contain>
        <Label>Email</Label>
        <Input type="text"></Input>
        <Div>
        <Label>Password</Label>
        <p>Forget Password?</p>
        </Div>
        
        <Input type="password"></Input>
        <Inputs type="submit" value="Login"></Inputs>
        <Contain>
        <h3>Don't have any account? <Link to="/Seller">Register</Link></h3>
        </Contain>
      </Form>

  
    </Container>
  )
}

export default Login;