import React from 'react';
import {Container,Contain,Product,Image,Item,Heading} from './ShopElement';
import {ShopData} from './Data';
import { Link } from 'react-router-dom';

const Shops = () => {
  return (
    <Container>

      <Heading>All Shops</Heading>
      <Contain>
        {
           ShopData.map((value,index)=>{
            return(
              <Product key={index}>
                
                <Link to={value.link}>
                <Item>
                <Image src={value.image}></Image>
                <h4>{value.name}</h4>
                </Item>
              </Link>
              </Product>
            )
          })
        }

      </Contain>
    </Container>
  )
}

export default Shops;