import styled from 'styled-components';

export const Container = styled.div`
 display: flex;
 width: 98.9vw;
 /* margin: 40px; */
 background-color:#f5f5Dc;
`;
export const AddressContain = styled.div`
 width: 20vw;
 text-align: center;
 padding:30px 15px;
 margin:25px;
 height:70vh;
 background-color: #ffff;
 position: sticky;
 top:80px;
 @media screen and (max-width:700px){
     display:none;
 }
`;
export const ContactSection = styled.div`
 text-align: left;
 line-height:5px;
`;
export const Image = styled.img`
 width: 150px;
 height: 150px;
 border-radius:10px;

 @media screen and (max-width:700px){
     width: 80px;
     height: 80px;
     margin-right:10px;
 }
 
`;
export const IconSection = styled.div`
 display: flex;
 justify-content:center;
`;
export const Icon = styled.div`
 margin: 0 5px;
`;
export const WebContain = styled.div`
 display: flex;
 justify-content: space-between;
 align-self: center;
 text-align:center;
 margin-top:-20px;
`;
export const ProductContain = styled.div`
 width: 80vw;
 margin: 25px;
`;
export const BannerImage = styled.img`
 object-fit: contain;
 width: 100%;
`;
export const ProductItem = styled.div`
 /* display: flex;
 justify-content: center;
 align-items: center; */
`;
export const Wrapper = styled.div`
 display: flex;
 flex-wrap: wrap;
 justify-content:left; 
 /* align-items: center; */
 /* margin:auto 0; */
 /* margin:0 auto; */
 /* line-height:2px; */
 
`;
export const ProductWrap = styled.div`
 box-shadow: 0 4px 8px 0 rgba(0,0,0,0.15);
 padding: 10px;
 width:250px;
 height: 385px;
 /* display: flex;
 flex-direction: column;
 justify-content: center; */
 align-items: center;
 margin:10px;
 background-color: #ffff;


`;
export const OfferSection = styled.div`
 display: flex;
`;
export const ProductImage = styled.img`
 width:200px;
 height:200px;
 cursor: pointer;
 &:hover{
     transform: translateY(2px);
 }

`;
export const H5 = styled.h4`
 margin:-10px 0;
`;
export const H4 = styled.h4`
 text-decoration: line-through;
 margin-right: 10px;
`;
export const PriceSection = styled.div`
 display: flex;
 justify-content:space-between;
 align-items: center;
 margin-top:-20px;
`;
export const Button = styled.button`
 display: flex;
 justify-content: space-between;
 align-items: center;
 width: 100px;
 height:35px;
 border-radius: 100px;
 border:none;
 border: 1px solid;
 padding: 15px;
 cursor: pointer;
 transition: all 0.5s;
 &:hover{
     background-color: grey;
     color: white;
 }


`;
export const LogoSection = styled.div`
 display: none;
 @media screen and (max-width:700px){
     display:flex;
     justify-content: center;
     align-items: center;
 }

`;