import { Facebook, Instagram, Twitter } from '@mui/icons-material';
import React from 'react';
import { Container,Contain,AddressContain,FormContain,
           Image,WrapperDetail,WebContain,IconSection,Icon,DIv,Section,
           Form,Label,Input,Textarea,Inpu,Inputs } from './ContactElement';


const Contact = () => {
  return (
    <Container>
      <Contain>
        <AddressContain>
          <Image src={require("../../assets/images/Contact.jpg")}></Image>
          <WrapperDetail>
            <h3>Address:</h3>
            <p>Madurai,Tamil Nadu.</p>
            <h3>Phone No:</h3>
            <p>+91 98765 43210</p>
            <h3>WebSite</h3>
            <WebContain>
              <p>https://www.address.co.in</p>
              <p>Visit This Site</p>
            </WebContain>
            <h3>Follow Us</h3>
            <IconSection>
              <Icon><Facebook/></Icon>
              <Icon><Twitter/></Icon>
              <Icon><Instagram/></Icon>
            </IconSection>
          </WrapperDetail>
          
        </AddressContain>
        <FormContain>
          <h2>Contact Us</h2>
          <Form>
            <DIv>
              <Section>
              <Label>Name</Label>
              <Input type="text"></Input>
              </Section>
              <Section>
              <Label>Email</Label>
              <Input type="text"></Input>
              </Section>
            </DIv>
            <Label>Subject</Label>
            <Inpu type="text"></Inpu>
            <Label>Description</Label>
            <Textarea type="text"></Textarea>
            <Inputs type="submit"></Inputs>
          </Form>
        </FormContain>
      </Contain>
    </Container>
  )
}

export default Contact