import { LocalMall } from '@mui/icons-material';
import React, { useState } from 'react';
import {Container,Wrapper,ProductWrap,ProductImage,OfferSection,H4,PriceSection,
       Button,ButtonSection,Icons,Input}from './ProductElement';

const Product = ({items,addToCart,decreaseCount,increaseCount}) => {
  return (
    <Container>
          <Wrapper>
            {
              items && items.map((data,index)=>{
                return(
                  <ProductWrap key={data.index}>
                    <ProductImage src={data.img} alt="image"></ProductImage>
                    <h4>{data.name}</h4>
                    <OfferSection>
                      <h5>&#8377; {data.price[0]}.00</h5>
                      <H4>&#8377; {data.price[1]}.00</H4>
                    </OfferSection>
                    {!data.inCart ? (
                    <PriceSection>
                      <Button onClick={() => addToCart(index)}><LocalMall/>CART</Button>
                      <ButtonSection>
                          <Icons  onClick={() => decreaseCount(index)}>-</Icons>
                          <Input readonly type="text" value={data.quantity}></Input>
                          <Icons  onClick={() => increaseCount(index)}>+</Icons>
                      </ButtonSection>
                    </PriceSection>
                    ):(
                      <p>
                        Item added
                      </p>
                    )}
                  </ProductWrap>
                )
              })
            }
          </Wrapper>
    </Container>
  )
}

export default Product;