import data from './Data'
import FaqDetails from './FaqDetails';
import styled from 'styled-components'


const Container = styled.div`
 text-align: center;
 background-color:#f5f5Dc;
 height: 100vh;
 margin-top: -20px;
 padding-top: 70px;
`;
const Contain = styled.div`
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 
`;
const FAQ = () => {
  return (
    <Container>
      <h1>FAQ</h1>
      <Contain>
      {
        data.map((Element) =>{
          const {id} = Element;
          return <FaqDetails key={id} {...Element}/>
        })
      }
      </Contain>
    </Container>
  )
}

export default FAQ