import React from 'react'
import Menulist from './MenuList';
import MenuIcon from '@mui/icons-material/Menu';
import { Nav,Ul,Li,A,Icons,Content,Select,Option,DropdownContent } from './NavbarElement';
import { Link } from 'react-router-dom';



const Navbar = () => {
  const[click, setClick] = React.useState(false);
  const menuitem = Menulist.map(({ url,nam }, index) =>{
    return(
      <Li key={index}>
        <A href={url}>{nam}</A>
      </Li>
    );
  });
  const handleClick=()=>{
    setClick(!click)
  }
  return (
    <Nav>
      <Content>
      <div>
        <h1>logo</h1>
      </div>
      <DropdownContent>
        <Select>
          <Option>Bags</Option>
          <Option>Bakery</Option>
          <Option>Books</Option>
          <Option>Cloths</Option>
          <Option>Furniture</Option>
          <Option value="www.google.com">Grocery</Option>
          <Option>Makeup</Option>
        </Select>
      </DropdownContent>
      </Content>
      <Icons onClick={handleClick}>
        <div className={click ? "MenuIcon" : "Close"}>
          <MenuIcon/>
        </div>
      </Icons>
      <Ul>{menuitem}</Ul>
      
      
    </Nav>
  )
}

export default Navbar;